import { reactive, toRefs } from 'vue';
import { useTranslate } from '@demo/use-translate';
const i18n = {
    'zh-CN': {
        errorInfo: '错误提示',
        errorText: '请求失败，点击重新加载',
        pullRefresh: '下拉刷新',
        finishedText: '没有更多了',
    },
    'en-US': {
        errorInfo: 'Error Info',
        errorText: 'Request failed. Click to reload',
        pullRefresh: 'PullRefresh',
        finishedText: 'Finished',
    },
};
export default {
    setup() {
        const t = useTranslate(i18n);
        const state = reactive({
            list: [
                {
                    items: [],
                    refreshing: false,
                    loading: false,
                    error: false,
                    finished: false,
                },
                {
                    items: [],
                    refreshing: false,
                    loading: false,
                    error: false,
                    finished: false,
                },
                {
                    items: [],
                    refreshing: false,
                    loading: false,
                    error: false,
                    finished: false,
                },
            ],
        });
        const onLoad = (index) => {
            const list = state.list[index];
            list.loading = true;
            setTimeout(() => {
                if (list.refreshing) {
                    list.items = [];
                    list.refreshing = false;
                }
                for (let i = 0; i < 10; i++) {
                    const text = list.items.length + 1;
                    list.items.push(text < 10 ? '0' + text : String(text));
                }
                list.loading = false;
                list.refreshing = false;
                // show error info in second demo
                if (index === 1 && list.items.length === 10 && !list.error) {
                    list.error = true;
                }
                else {
                    list.error = false;
                }
                if (list.items.length >= 40) {
                    list.finished = true;
                }
            }, 1000);
        };
        const onRefresh = (index) => {
            state.list[index].finished = false;
            onLoad(index);
        };
        return {
            ...toRefs(state),
            t,
            onLoad,
            onRefresh,
        };
    },
};
